import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Modules from '../views/Modules.vue'
import Partnership from '../views/Partnership.vue'
import Media from '../views/Media.vue'
import Contact from '../views/Contact.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'Home', component: Home, meta: { layout: 'main-layout' } },
  { path: '/modules', name: 'Modules', component: Modules, meta: { layout: 'main-layout' } },
  { path: '/partnership', name: 'Partnership', component: Partnership, meta: { layout: 'main-layout' } },
  { path: '/media', name: 'Media', component: Media, meta: { layout: 'main-layout' } },
  { path: '/contact', name: 'Contact', component: Contact, meta: { layout: 'main-layout' } }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
